html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

div,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

html {
  box-sizing: border-box;
  transition: font-size 0.4s ease-out;
}

body {
  overflow-x: hidden;
  min-height: 100vh;
  line-height: 1;
  text-rendering: optimizeSpeed;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  display: inline-block;
  text-decoration: none;
  color: #000;
}

ol,
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

.container {
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
}

.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #010001;
  z-index: 3;
  transition: all 1s;
  opacity: 0.4;
  visibility: visible;
}
.preloader__off {
  opacity: 0;
  visibility: hidden;
}
.preloader .loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 54px;
  height: 54px;
  border: 6px solid #ffbb55;
  border-top-color: #ffffff;
  border-radius: 50%;
  animation: 1s spin infinite linear;
}

.preloader-video {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #010001;
  z-index: 3;
  transition: all 1s;
  opacity: 0.4;
  visibility: visible;
}
.preloader-video__off {
  opacity: 0;
  visibility: hidden;
}
.preloader-video .loader-video {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 54px;
  height: 54px;
  border: 6px solid #FFFFFF;
  border-top-color: inherit;
  border-radius: 50%;
  animation: 1s spin infinite linear;
}

@keyframes spin {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
.frame {
  position: relative;
  z-index: 1;
}

