.header-container {
  display: flex;
  justify-content: space-between;
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  border-bottom: 1.5px solid rgba(1, 0, 1, 0.05);
  margin-bottom: 125px;
  padding-right: 80px;
  padding-left: 100px;
}

.header-box {
  display: flex;
  height: 83px;
}
.header-box__menu {
  display: flex;
  align-items: center;
}
.header-box__logo {
  margin-right: 31px;
}
.header-box__logo--img {
  width: 102px;
  height: 50px;
}
.header-box__links {
  font-family: "Inter-Medium";
  font-size: 16px;
}
.header-box__links ul {
  display: flex;
}
.header-box__links ul li {
  margin-right: 28px;
}

.search {
  display: flex;
  align-items: center;
}
.search-box {
  display: flex;
  align-items: center;
  border-radius: 8px;
  border: 1px solid #e9e9e9;
  margin-right: 17px;
}
.search-box input {
  height: 47px;
  outline: none;
  border: none;
  padding-left: 41px;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  font-family: "Inter-Medium";
  font-size: 14px;
}
.search-box input::placeholder {
  color: #010001;
  opacity: 0.4;
}
.search-box__select {
  position: relative;
  width: 150px;
  height: 47px;
  margin: 0 auto;
}
.search-box__select[data-state=active] .search-box__select--title::before {
  transform: translate(-3px, -50%) rotate(-45deg);
}
.search-box__select[data-state=active] .search-box__select--title::after {
  transform: translate(3px, -50%) rotate(45deg);
}
.search-box__select[data-state=active] .search-box__select-wrapper {
  opacity: 1;
  z-index: 9;
  transform: translate(0, 0px);
}
.search-box__select[data-state=active] .search-box__select--label + .search-box__select--input + .search-box__select--label {
  max-height: 38px;
}
.search-box__select--title {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  font-family: "Inter-Medium";
  font-size: 16px;
  line-height: 17px;
  padding: 8px 16px;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
}
.search-box__select--title::before, .search-box__select--title::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 16px;
  display: block;
  width: 10px;
  height: 2px;
  transition: all 0.3s ease-out;
  background-color: #333333;
  transform: translate(-3px, -50%) rotate(45deg);
}
.search-box__select--title::after {
  transform: translate(3px, -50%) rotate(-45deg);
}
.search-box__select--input {
  display: none;
}
.search-box__select--input:checked + label {
  color: #010001;
}
.search-box__select--input:disabled + label {
  opacity: 0.6;
  pointer-events: none;
}
.search-box__select--label {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 0 16px;
  font-family: "Inter-Medium";
  font-size: 16px;
  line-height: 20px;
  color: #00000083;
  cursor: pointer;
  overflow: hidden;
}
.search-box__select--label + input + .search-box__select--label {
  border-top: 0 solid #C7CCD160;
}
.search-box__select--label:hover {
  background: rgba(1, 0, 1, 0.05);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  outline: 1px solid #000;
}
.search-box__select--label-first:hover {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}
.search-box__select--label-last:hover {
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}
.search-box__select-wrapper {
  position: absolute;
  top: 65px;
  left: 3px;
  display: flex;
  flex-direction: column;
  width: calc(100% - 6px);
  background-color: #ffffff;
  border: 1px solid #E9E9E9;
  border-radius: 8px;
  box-shadow: 0px 4px 18px rgba(1, 0, 1, 0.12);
  transition: all 0.3s ease-out;
  opacity: 0;
  transform: translate(0, -30px);
  z-index: -9;
}
.search-box__btn {
  height: 47px;
  outline: none;
  border: none;
  cursor: pointer;
  background-color: #fff;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}
.search-box__btn--img {
  width: 20px;
  height: 20px;
}
.search-box__btn--line {
  width: 1px;
  height: 25px;
  margin-left: 3px;
  margin-right: 3px;
  background-color: #c4c4c4;
}
.search-favorites {
  margin-right: 27px;
  outline: none;
  border: none;
  background-color: inherit;
  cursor: pointer;
}
.search-profile {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: inherit;
  border: 1.5px solid rgba(1, 0, 1, 0.12);
  border-radius: 14px;
  width: 119px;
  height: 48px;
  font-family: "Evolventa-Bold";
  font-size: 16px;
  cursor: pointer;
}
.search-profile img {
  margin-right: 9px;
}

